.l-main {
    margin-bottom: 65px;
    .sliders-item{
        min-height: 500px;
        img{
            width: 100%;
        }
    }
    .main-image-slider{
        position: relative;
        .slick-dots{
            position: absolute;
            bottom: 24px;
            right: 19%;
        }
    }
}
.slick-dots li button { 
    font-size: 0; 
    width: 8px;
    height: 8px;
    background: #0B0B0B;
    border-radius: 100%;
    position: relative;
} 

.slick-dots li.slick-active button:before { 
    background: transparent;
    border: 2px solid #0b0b0b;
    border-radius: 100%;
    content: "";
    display: block;
    height: 16px;
    position: absolute;
    width: 16px;
    top: 0;
    left: 0;
    transform: translate(-4px, -4px);

 } 


 .card{
    max-width: 268px;
    display: block;
    .prices{
        display: flex;
        align-items: baseline;
    }
 }

 .card-top{
    position: relative;
    max-height: 340px;
    overflow: hidden;
    margin-bottom: 20px;;
}


.card-flag{
    position: absolute;
    width: 50px;
    height: 22px;
    font-style: normal;
    font-weight: 900;
    font-size: 13px;
    line-height: 16px;
    display: flex;
    align-items: center;
    text-transform: uppercase;
    color: #FFFFFF;
    text-align: center;
    display: flex;
    align-items: center;
    justify-content: center;
}

.card-sale{
    bottom: 10px;
    left: 10px;
    background-color: #FF3737;
}

.card-sale:after{
    border-left: 20px solid transparent;
    border-right: 0 solid transparent;
    border-top: 10px solid #FF3737;
    bottom: -6px;
    left: 2px;
    content: "";
    height: 0;
    position: absolute;
    width: 20px;
}

.card-new{
    top: 10px;
    right: 10px;
    background-color: #6FB662;
}
.card-new:after{
    border-left: 20px solid transparent;
    border-right: 0 solid transparent;
    border-top: 10px solid #6FB662;
    bottom: -6px;
    left: 2px;
    content: "";
    height: 0;
    position: absolute;
    width: 20px;
}

.card-colors{
    display: flex;
    align-items: center;
    font-family: 'Montserrat';
    .color-item{
        width: 14px;
        height: 14px;
        border-radius: 100%;
        border: 1px solid transparent;
        margin-right: 6px;
    }    
}

.more-colors{
    font-style: normal;
    font-weight: 500;
    font-size: 14px;
    line-height: 17px;
    display: flex;
    align-items: center;
    color: #7F7F7F;
}
.newPrice{
    font-style: normal;
    font-weight: 700;
    font-size: 19px;
    line-height: 23px;
    display: flex;
    align-items: center;
    color: #0B0B0B;
    margin-right: 10px;;
}
.oldPrice{
    font-style: normal;
    font-weight: 500;
    font-size: 16px;
    line-height: 129.99%;
    display: flex;
    align-items: center;
    letter-spacing: 0.0155em;
    text-decoration-line: line-through;
    color: #7F7F7F;
}
.card-name{
    font-style: normal;
    font-weight: 700;
    font-size: 14px;
    line-height: 24px;
    align-items: center;
    letter-spacing: 0.0155em;
    color: #7F7F7F;
    margin-bottom: 10px;
    text-overflow: ellipsis; // обрізання довгих блоків з текстом
    overflow: hidden; // приховати текст, що виходить за межі блоку
    width: 100%; // ширина блоку з текстом
    white-space: nowrap; // заборона на перенесення тексту
}
.card-infoLine{
    display: flex;
    justify-content: space-between;
    text-overflow: ellipsis; // обрізання довгих блоків з текстом
    overflow: hidden; // приховати текст, що виходить за межі блоку
    width: 100%; // ширина блоку з текстом
    white-space: nowrap; // заборона на перенесення тексту
}

.new-collection-block{
    padding: 65px 0px;
}

.sale-block{
    padding: 65px 0px;
}
.last-seen{
    padding: 65px 0px;    
 }
 .last-seen{
    padding-bottom: 160px;
 }


 .sale-block .slider-name{
    transform: rotate(-90deg) translateX(-10px) translateY(-41px);
}

.last-seen .slider-name{
    width: 200px;
    transform: rotate(-90deg) translateX(-79px) translateY(-61px);
}



 .side-custom-arrows{
    .slick-next{
        background-repeat: no-repeat;
        background-position: 30% center;
        height: 81%;
        position: absolute;
        right: 0;
        top: 0px;
        width: 60px;
        
        &:hover{
            background-color:  rgba(250, 250, 250, 0.2);
            background-image: url(../img/icons/arrow-right.svg);
        }
    }
    .slick-prev{
        background-repeat: no-repeat;
        background-position: 30% center;
        height: 81%;
        position: absolute;
        z-index: 20;
        left: 0;
        top: 0px;
        width: 60px;        
        &:hover{
            background-color:  rgba(250, 250, 250, 0.2);
            background-image: url(../img/icons/arrow-left.svg);
        }
    }    
}

.slider-name{
    position: absolute;
    left: 0px;
    /* top: 0px; */
    transform: rotate(-90deg) translateX(-51px) translateY(-41px);
    color: #7f7f7f;
    font-style: normal;
    letter-spacing: .0155em;
    font-weight: 700;
    width: 146px;
}



@media screen and (max-width: 900px){
    .new-collection-slider, .sale-slider, .last-seen-slider{
        max-width: 600px;;
        margin: 0 auto;
    } 
}


@media screen and (max-width: 730px){
    .slider-name , .sale-block .slider-name,  .last-seen .slider-name{          
        position: relative;      
        transform: none;
        width: auto;
        margin-bottom: 30px;
    }
    .new-collection-block{
        padding: 15px 0px;
    }
    
    .sale-block{
        padding: 15px 0px;
    }
    .last-seen{
        padding: 15px 0px;    
     }
     .main-image-slider{
        min-height: 468px;
     }

     .l-main {
        margin-bottom: 25px;
    }

}


@media screen and (max-width: 590px){
    .new-collection-slider, .sale-slider, .last-seen-slider{
        max-width: 310px;;
        margin: 0 auto;
    } 
    .last-seen-slider{
        max-width: 100%;
        margin: 0 auto;
    } 
    .card {
        margin-right: 15px;
        max-width: 150px;
        margin-bottom: 40px;
    }
    .card-top{
        height: 230px;
        width: 150px;
        margin-bottom: 0px;
    }

    .card-name{
        font-weight: 600;
        margin-bottom: 5px;
    }
    .card-infoLine{
        flex-direction: column;
    }
    .slick-slide .card img {
        display: block;
        width: auto;
        height: 100%;
        object-fit: cover;
    }

    .card-infoLine .prices{
        order: 2;        
    }

    .card-colors{
        margin-bottom: 5px;;
    }
    .newPrice{
        font-size: 16px;;
    }
    .oldPrice{
        font-size: 16px;;
    }
    .main-slider .slick-slide img{
        min-height: 468px;
        /* width: 100%; */
        object-fit: cover;

    }

}