*:where(:not(iframe, canvas, img, svg, video):not(svg *)) {
  all: unset;
  display: revert;
}

*,
*::before,
*::after {
  box-sizing: border-box;
}

a,
button {
  cursor: pointer;
}

ol,
ul {
  list-style: none;
}

img {
  max-width: 100%;
  vertical-align: bottom;
}

table {
  border-collapse: collapse;
}

textarea {
  white-space: revert;
}

html {
  font-size: 100%;
}

body {
  font-family: "Montserrat", "Helvetica Neue", "Helvetica", "Hiragino Sans", "Hiragino Kaku Gothic ProN", "Arial", "Yu Gothic", "Meiryo", sans-serif;
  font-weight: 500;
  font-size: 1rem;
  color: black;
  background-color: #F5F5F5;
}

/* width */
::-webkit-scrollbar {
  width: 6px;
}

/* Track */
::-webkit-scrollbar-track {
  box-shadow: inset 0 0 5px grey;
  border-radius: 0px;
}

/* Handle */
::-webkit-scrollbar-thumb {
  background: #191919;
  border-radius: 0px;
}

.content {
  max-width: 1160px;
  margin: 0 auto;
  padding: 0px 15px;
}

.blackline {
  background-color: #0B0B0B;
  padding: 16px 0px;
  text-align: center;
}
.blackline .norm {
  color: #F5F5F5;
  text-transform: uppercase;
  font-weight: 300;
  letter-spacing: 0.4px;
  font-size: 14px;
}

.menu-line {
  height: 90px;
  display: grid;
  grid-template-columns: repeat(3, 1fr);
  text-align: center;
  align-items: center;
  padding: 0 55px 0 39px;
}

.open-menu {
  height: 20px;
  width: 27px;
  cursor: pointer;
  margin-right: 44px;
}
.open-menu:hover span {
  background-color: #7D5555;
  transition: all ease 0.3s;
}
.open-menu span {
  display: block;
  width: 100%;
  height: 1px;
  background-color: #0B0B0B;
}
.open-menu span:not(:last-child) {
  margin-bottom: 8px;
}

.sidenav {
  height: 100%;
  width: 0;
  position: fixed;
  z-index: 1;
  top: 0;
  opacity: 0;
  left: -100%;
  background-color: #fff;
  overflow-x: hidden;
  transition: 0.4s;
  padding-top: 60px;
  width: 250px;
}

.sidenav a {
  padding: 8px 8px 8px 32px;
  text-decoration: none;
  font-size: 18px;
  color: #363636;
  display: block;
  transition: 0.4s;
  font-weight: 300;
}

.sidenav a:hover {
  color: #000;
}

.sidenav .closebtn {
  position: absolute;
  top: 0;
  right: 2px;
  font-size: 36px;
  margin-left: 50px;
}

.overflow-block {
  background-color: rgba(1, 1, 1, 0.4);
  position: absolute;
  top: 0px;
  left: 0px;
  width: 100%;
  height: 100%;
  opacity: 0;
  pointer-events: none;
  transition: all ease-in-out 0.3s;
}

@media screen and (max-height: 450px) {
  .sidenav {
    padding-top: 15px;
  }

  .sidenav a {
    font-size: 18px;
  }
}
.main-menu, .right-menu-info, .left-menu {
  display: flex;
  align-items: center;
}

.right-menu-info {
  justify-content: flex-end;
}

.main-menu-item {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 800;
  font-size: 14px;
  line-height: 28px;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  color: #0B0B0B;
  cursor: pointer;
}
.main-menu-item:hover {
  color: #000;
}
.main-menu-item:not(:last-child) {
  margin-right: 42px;
}

.search {
  position: relative;
  height: 50px;
}

.search .input {
  background-color: #fff;
  border: 0;
  font-size: 18px;
  padding: 15px;
  height: 50px;
  width: 50px;
  transition: width 0.3s ease;
  position: absolute;
  right: -11px;
  width: 30px;
  background-color: transparent;
}

.btn {
  background-color: transparent;
  border: 0;
  cursor: pointer;
  font-size: 24px;
  position: absolute;
  top: 0;
  right: 0;
  height: 50px;
  width: 37px;
  transition: transform 0.3s ease;
}

.btn:focus,
.input:focus {
  outline: none;
}

.search.active .input {
  border-radius: 2px;
  width: 272px;
  margin-right: 21px;
  background-color: rgba(255, 255, 255, 0.4);
}

.search .btn {
  margin-right: 27px;
}
.search .search-img {
  height: 25px;
  width: 24px;
  background: url("../img/icons/search.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.personal-cabinet {
  height: 24px;
  width: 24px;
  background: url("../img/icons/person.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  margin-right: 40px;
}

.cart-icon {
  width: 27px;
  height: 28px;
  background: url("../img/icons/bag.svg");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
  position: relative;
}

.cart-amount {
  position: absolute;
  bottom: -10px;
  right: -10px;
  background-color: #6FB662;
  border-color: #fff;
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 700;
  font-size: 11px;
  line-height: 13px;
  border: 1px solid #fff;
  color: #fff;
  padding: 5px;
  border-radius: 100%;
}

.right-menu-info--icon {
  cursor: pointer;
}

.menu-line {
  position: relative;
}

.sub-menu {
  background: #F5F5F5;
  position: absolute;
  left: 0px;
  width: 100%;
  padding: 100px 40px 60px;
  top: 57px;
  z-index: 9;
  box-sizing: border-box;
  display: flex;
  flex-direction: row;
  align-items: flex-start;
  justify-content: space-between;
  border-bottom: 1px solid #e5e5e5;
  opacity: 0;
  visibility: hidden;
  min-height: 0;
  max-height: 0;
  overflow: hidden;
  transition: visibility 0.5s ease, opacity 0.5s ease, min-height 0.5s ease, max-height 0.5s ease;
}

.main-menu-item.hover .sub-menu {
  opacity: 1;
  visibility: visible;
  max-height: initial;
  min-height: auto;
}

.submenu-name {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 32px;
  line-height: 28px;
  width: 157px;
  letter-spacing: 1.5px;
  text-transform: uppercase;
  color: #0B0B0B;
  transform: rotate(-90deg) translateY(-26px) translateX(-111px);
  position: absolute;
  left: 0px;
}

.sub-menu:before {
  content: "";
  height: 1px;
  width: 100%;
  position: absolute;
  top: 30px;
  left: 0px;
  background-repeat: repeat;
  background-image: url(../img/icons/dashed.svg);
}
.sub-menu .left {
  display: flex;
  text-align: left;
  margin-right: 93px;
  padding-left: 92px;
}
.sub-menu .left ul:not(:last-child) {
  margin-right: 84px;
}
.sub-menu .left li {
  font-family: "Montserrat";
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 129.99%;
  /* or 21px */
  display: flex;
  align-items: center;
  letter-spacing: 0.0155em;
  margin-bottom: 26px;
  text-transform: none;
  position: relative;
}
.sub-menu .left li a:hover {
  text-decoration: underline;
}
.sub-menu .right {
  display: flex;
  justify-content: space-between;
}

.menu-illustrations {
  width: 270px;
  height: 363px;
  position: absolute;
  bottom: 0px;
  margin-right: 80px;
  left: 47%;
}

.inform-banner {
  width: 546px;
  height: 260px;
  position: relative;
  text-align: right;
}
.inform-banner img {
  width: 100%;
}
.inform-banner:before {
  content: "";
  display: block;
  position: absolute;
  display: block;
  height: 73px;
  left: 131px;
  position: absolute;
  top: -55px;
  width: 97px;
  background: url("../img/illustrations/cat.png");
  background-repeat: no-repeat;
  background-position: center center;
  background-size: cover;
}

.sidenav .mobile-main-menu a.nemu-name {
  font-weight: 600;
  display: flex;
  justify-content: space-between;
}

.open-close {
  width: 15px;
  height: 15px;
  position: relative;
  margin-right: 20px;
  transition: all ease 0.3s;
}

.open-close:after {
  content: "";
  display: block;
  height: 1px;
  width: 100%;
  background-color: #000;
  position: absolute;
  top: 7px;
  transition: all ease 0.3s;
}

.open-close:before {
  content: "";
  display: block;
  height: 1px;
  width: 100%;
  background-color: #000;
  position: absolute;
  transform: rotate(90deg);
  transform-origin: center;
  top: 7px;
}

.hidden-mobile-submenu {
  height: 0px;
  visibility: hidden;
  opacity: 0;
  transition: all ease-in-out 0.3s;
}

.mobile-main-menu.open .hidden-mobile-submenu {
  height: 100%;
  visibility: visible;
  opacity: 1;
}

.mobile-main-menu.open .open-close:before {
  transform: rotate(0deg);
  transform-origin: center;
}

@media screen and (max-width: 1650px) {
  .inform-banner {
    height: auto;
    width: 386px;
  }

  .menu-illustrations {
    left: auto;
    right: 23%;
  }

  .sub-menu .left ul:not(:last-child) {
    margin-right: 50px;
  }
}
@media screen and (max-width: 1500px) {
  .menu-illustrations {
    display: none;
  }

  .inform-banner:before {
    left: 7px;
  }
}
@media screen and (max-width: 1200px) {
  .main-menu {
    display: none;
  }
}
@media screen and (max-width: 920px) {
  .search .btn {
    margin-right: 4px;
  }

  .personal-cabinet {
    margin-right: 12px;
  }

  .menu-line {
    align-items: center;
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    height: 90px;
    padding: 0 15px 0 15px;
  }

  .search {
    position: absolute;
    left: 102px;
  }

  .search.active .input {
    background-color: white;
    border: 1px solid #000;
  }

  .search .input {
    right: auto;
    left: -11px;
  }
}
.l-main {
  margin-bottom: 65px;
}
.l-main .sliders-item {
  min-height: 500px;
}
.l-main .sliders-item img {
  width: 100%;
}
.l-main .main-image-slider {
  position: relative;
}
.l-main .main-image-slider .slick-dots {
  position: absolute;
  bottom: 24px;
  right: 19%;
}

.slick-dots li button {
  font-size: 0;
  width: 8px;
  height: 8px;
  background: #0B0B0B;
  border-radius: 100%;
  position: relative;
}

.slick-dots li.slick-active button:before {
  background: transparent;
  border: 2px solid #0b0b0b;
  border-radius: 100%;
  content: "";
  display: block;
  height: 16px;
  position: absolute;
  width: 16px;
  top: 0;
  left: 0;
  transform: translate(-4px, -4px);
}

.card {
  max-width: 268px;
  display: block;
}
.card .prices {
  display: flex;
  align-items: baseline;
}

.card-top {
  position: relative;
  max-height: 340px;
  overflow: hidden;
  margin-bottom: 20px;
}

.card-flag {
  position: absolute;
  width: 50px;
  height: 22px;
  font-style: normal;
  font-weight: 900;
  font-size: 13px;
  line-height: 16px;
  display: flex;
  align-items: center;
  text-transform: uppercase;
  color: #FFFFFF;
  text-align: center;
  display: flex;
  align-items: center;
  justify-content: center;
}

.card-sale {
  bottom: 10px;
  left: 10px;
  background-color: #FF3737;
}

.card-sale:after {
  border-left: 20px solid transparent;
  border-right: 0 solid transparent;
  border-top: 10px solid #FF3737;
  bottom: -6px;
  left: 2px;
  content: "";
  height: 0;
  position: absolute;
  width: 20px;
}

.card-new {
  top: 10px;
  right: 10px;
  background-color: #6FB662;
}

.card-new:after {
  border-left: 20px solid transparent;
  border-right: 0 solid transparent;
  border-top: 10px solid #6FB662;
  bottom: -6px;
  left: 2px;
  content: "";
  height: 0;
  position: absolute;
  width: 20px;
}

.card-colors {
  display: flex;
  align-items: center;
  font-family: "Montserrat";
}
.card-colors .color-item {
  width: 14px;
  height: 14px;
  border-radius: 100%;
  border: 1px solid transparent;
  margin-right: 6px;
}

.more-colors {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  display: flex;
  align-items: center;
  color: #7F7F7F;
}

.newPrice {
  font-style: normal;
  font-weight: 700;
  font-size: 19px;
  line-height: 23px;
  display: flex;
  align-items: center;
  color: #0B0B0B;
  margin-right: 10px;
}

.oldPrice {
  font-style: normal;
  font-weight: 500;
  font-size: 16px;
  line-height: 129.99%;
  display: flex;
  align-items: center;
  letter-spacing: 0.0155em;
  text-decoration-line: line-through;
  color: #7F7F7F;
}

.card-name {
  font-style: normal;
  font-weight: 700;
  font-size: 14px;
  line-height: 24px;
  align-items: center;
  letter-spacing: 0.0155em;
  color: #7F7F7F;
  margin-bottom: 10px;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
  white-space: nowrap;
}

.card-infoLine {
  display: flex;
  justify-content: space-between;
  text-overflow: ellipsis;
  overflow: hidden;
  width: 100%;
  white-space: nowrap;
}

.new-collection-block {
  padding: 65px 0px;
}

.sale-block {
  padding: 65px 0px;
}

.last-seen {
  padding: 65px 0px;
}

.last-seen {
  padding-bottom: 160px;
}

.sale-block .slider-name {
  transform: rotate(-90deg) translateX(-10px) translateY(-41px);
}

.last-seen .slider-name {
  width: 200px;
  transform: rotate(-90deg) translateX(-79px) translateY(-61px);
}

.side-custom-arrows .slick-next {
  background-repeat: no-repeat;
  background-position: 30% center;
  height: 81%;
  position: absolute;
  right: 0;
  top: 0px;
  width: 60px;
}
.side-custom-arrows .slick-next:hover {
  background-color: rgba(250, 250, 250, 0.2);
  background-image: url(../img/icons/arrow-right.svg);
}
.side-custom-arrows .slick-prev {
  background-repeat: no-repeat;
  background-position: 30% center;
  height: 81%;
  position: absolute;
  z-index: 20;
  left: 0;
  top: 0px;
  width: 60px;
}
.side-custom-arrows .slick-prev:hover {
  background-color: rgba(250, 250, 250, 0.2);
  background-image: url(../img/icons/arrow-left.svg);
}

.slider-name {
  position: absolute;
  left: 0px;
  /* top: 0px; */
  transform: rotate(-90deg) translateX(-51px) translateY(-41px);
  color: #7f7f7f;
  font-style: normal;
  letter-spacing: 0.0155em;
  font-weight: 700;
  width: 146px;
}

@media screen and (max-width: 900px) {
  .new-collection-slider, .sale-slider, .last-seen-slider {
    max-width: 600px;
