.l-footer {
  padding: 67px 0px 97px 0px;
}
.l-footer .footer-content, .l-footer .footer-left {
  display: flex;
  justify-content: space-between;
}
.l-footer .footer-ul {
  margin-right: 50px;
}
.l-footer .footer-ul.footer-ul-bold .footer-a {
  font-weight: 800;
}
.l-footer .footer-li {
  margin-bottom: 29px;
}
.l-footer .footer-a {
  font-style: normal;
  font-weight: 500;
  font-size: 14px;
  line-height: 17px;
  /* identical to box height */
