  position: relative;
}

.footer-logo-place {
  transform: rotate(-90deg);
  width: 115px;
  position: absolute;
  left: -118px;
  top: 47px;
}

.links-place-group {
  margin-bottom: 64px;
}
.links-place-link {
  margin-right: 30px;
  transition: all ease-in-out 0.3s;
}
.links-place-link:hover {
