  }

  .footer-right {
    margin-right: 10px;
  }

  .links-place-group {
    margin-bottom: 64px;
    display: flex;
    flex-direction: column;
    align-items: center;
  }

  .for-mobile-footer {
    display: flex;
    flex-direction: column;
    align-items: center;
  }
}